import { Component, OnInit } from '@angular/core';
import {trigger,state,style,transition,animate} from "@angular/animations"
import { PathModel } from "app/menu/PathModel";


@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss'],
   animations:[
    trigger("animState",
    [
      state('inactive',style({
        'text-decoration':'none',
          transform: 'scale(1)'

      })),
      state('active',style({       
      'text-decoration':'underline',
        transform: 'scale(1.4)'
      })),
      transition('inactive => active',animate(130)),
      transition('active => inactive',animate('30ms 400ms')),
    ]    
    )
  ]
})
export class MenuComponent implements OnInit {

  public menuItems:PathModel[]=[
    {title:'Products',path:'product'},
    {title:'New Reviews',path:'review'},
    {title:'Customer',path:'customer'}
    ];
  public currentItem:string=this.menuItems[0].title;
    
  constructor() { }

  ngOnInit() {
  }

}



