import { Component, OnInit,ViewChild } from '@angular/core';
import { CategoryComponent } from "app/category/category.component";
import { ProductService } from "app/product/product.service";
import { product } from "app/product/product";
import { MdCheckbox, MdButton } from '@angular/material';
import {trigger,state,style,transition,animate} from "@angular/animations"
import { Router } from "@angular/router";


@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.scss'],
 animations:[
    trigger("animInfoState",
    [
      state('inactive',style({        
          transform: 'scale(0.1)'
      })),
      state('active',style({    
        transform: 'scale(1.0)'
      })),
      transition('inactive => active',animate('1000ms 400ms')),
      transition('active => inactive',animate('30ms 400ms')),
    ]    
    )
  ]
})
export class ProductComponent implements OnInit {

@ViewChild(CategoryComponent) categoryComponent:CategoryComponent;


  public products:product[];
  public msg:string="";
  public animInfoState='inactive';
  constructor(private productService:ProductService, private router: Router) {
    
  }
  
  ngOnInit() {
  
  this.loadProductByCategory(1);
  this.categoryComponent.CategorySelected.subscribe((id:number)=>
    {
        this.loadProductByCategory(id);
    });
  }

  private loadProductByCategory(categoryId:number)
  {   
      this.msg="Load...";
      this.animInfoState='active';
      this.products=[];
      this.productService.getProductByCategoryId(categoryId).subscribe((pMas)=>
      {
        this.animInfoState='inactive';
        this.msg="";
        this.products=pMas;
      });
   
  }

  public goToReview(id:number)
  {
    this.router.navigate(["/review",id]);
  }

}



