import { Component, OnInit,Inject,Output,EventEmitter } from '@angular/core';
import { category } from "app/category/category";
import { Http } from '@angular/http';
import { CategoryService } from "app/category/category.service";
import {trigger,state,style,transition,animate} from "@angular/animations"

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.scss'],
  animations:[
    trigger("overlayState",
    [
      state('inactive',style({
       
        'transform':'translateX(0px)'
      })),
      state('active',style({            
      'transform':'translateX(290px)'  
      })),
      transition('inactive => active',animate(200)),
      transition('active => inactive',animate('200ms 600ms')),
    ]    
    ),
   trigger("bigTextState",
    [
      state('inactive',style({
       
          transform:'scale(1.0)'
      })),
      state('active',style({            
      transform:'scale(1.11)'
      })),
      transition('inactive => active',animate(300)),
      transition('active => inactive',animate('200ms 200ms')),
    ]    
    ),
   trigger("selCategoryState",
    [
      state('inactive',style({
        opacity: '0.6',
        transform:'scale(1.0)',
        'background-image': 'linear-gradient(150deg,RGB(143,122,234) 0%,RGB(32,0,244) 100%)'   
         })),
      state('active',style({            
         transform:'scale(1.1)',
         opacity: '0.8',
         'background-image': 'linear-gradient(150deg,RGB(143,122,234) 0%,RGB(132,100,244) 100%)'   

      })),
      transition('inactive => active',animate(100)),
      transition('active => inactive',animate('200ms 200ms')),
    ]    
    )
  ]
})
export class CategoryComponent implements OnInit {

  public categoryMas:category[];
   overlayState:string="inactive";
   selectedCategoryId=1;

  constructor (private categoryService:CategoryService)
  {
   this.categoryService.getCategorys().subscribe(result => this.categoryMas=result);
  }  


  mouseOverCategory()
{
  this.overlayState='active';  
}
    private categoryClick(id)
  {
    this.selectedCategoryId=id;
    this.CategorySelected.emit(id);
  }

  @Output() public CategorySelected:EventEmitter<number>=new EventEmitter<number>();
  

  ngOnInit() {
 
  }

}



