import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/Observable";
import { review } from "app/review/review";

@Injectable()
export class ReviewsService {

  private  reviewsUrl(id:number):string
  {
    return `api/products/${id}/review` ;
  }

  constructor(private http: HttpClient) { }

    public getReviewsByProductId(id:number):Observable<review[]>
  {
      return this.http.get(this.reviewsUrl(id)).map(data=>{
        let res=data as review[];
        return res;
      });
  }

}



