import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { HttpClient } from "@angular/common/http";
import { Observable } from "rxjs/Observable";
import { review } from "app/review/review";
import { ReviewsService } from "app/review/reviews.service";

@Component({
  selector: 'app-review',
  templateUrl: './review.component.html',
  styleUrls: ['./review.component.scss']
})
export class ReviewComponent implements OnInit {

  public reviews:review[];
  public isReviewExsist:boolean;
  constructor(  private route: ActivatedRoute,private reviewsService:ReviewsService ) { }

  ngOnInit() {
   this.route.paramMap.subscribe(p=>
    {
      let id=parseInt(p.get('id'));
      this.reviewsService.getReviewsByProductId(id).subscribe(r=>{
         this.reviews=r;
         this.isReviewExsist=false;
         if(this.reviews!=null)
          if(this.reviews.length>0)
            this.isReviewExsist=true;

      })
    });
 
  }

}



