import { Injectable,Inject } from '@angular/core';
import { category } from "app/category/category";
import { Http } from '@angular/http';
import { Observable } from "rxjs/Observable";
import 'rxjs/add/operator/map';
import { environment } from "environments/environment";


@Injectable()
export class CategoryService {
 
  private categoryUrl:string='/api/Categorys';

 constructor(private http: Http, @Inject('ORIGIN_URL') private originUrl: string) {      
  console.log('originUrl='+originUrl); 
}


    public getCategorys():Observable<category[]>
    { 

  return this.http.get(this.categoryUrl).map(result => {
              let res = result.json() as category[];
               return res; 
              });

      // if(environment.production)    
      //     {
      //       return this.http.get(this.categoryUrl).map(result => {
      //         let res = result.json() as category[];
      //          return res; 
      //   });
      //     }
      // else
      //   { 
      //       return this.mockDataForCategory();
      //   };
   }

   private mockDataForCategory():Observable<category[]>
   {
    let dataCategory=[
            {
                categoryId:1,
                categoryName:'name1',
                categoryDescription:'description 13'
            },
              {
                categoryId:2,
                categoryName:'name2',
                categoryDescription:'description 2'
            },
              {
                categoryId:3,
                categoryName:'name3',
                categoryDescription:'description 3'
            }
              ,
              {
                categoryId:4,
                categoryName:'name4',
                categoryDescription:'description 4'
            }
          ];
           let observable = Observable.create(function (observer) {
              observer.next(dataCategory);            
           });
        return observable;
   }

}



