import { Injectable } from '@angular/core';
import { Http } from "@angular/http";
import { product } from "app/product/product";


@Injectable()
export class ProductService
{

  constructor(private http:Http) { }

  private productUrl:string='/api/products/Category/';

  public getProductByCategoryId(categoryId:number)
  {
    return this.http.get(this.productUrl+categoryId).map(result => 
     {
              let res = result.json() as product[];
               return res; 
     });  
  }
}



